
import { request,address } from "../utils/HttpRequest/request.js";
import sessionMenagement from '../utils/StorageManagement';
import XButton from 'vux/src/components/x-button/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import {mapState, mapActions} from 'vuex'
var flag = false;
export default {
directives: {
TransferDom
},
components: {
Flexbox,
FlexboxItem,
Divider,
Scroller,
XHeader,
XButton,
Loading
},
data () {
return {

member_id: '', //会员id
showNodata: false,  //显示图片还是地址列表
scrollHeight:'200px',
pageNum: 1,
loadingShow:true,
goodsList: {} , //往商品详情传的参数
amount_type:0,
}
},
computed: {
...mapState({
addressList: state => state.user.addressList
})
},
methods: {
...mapActions({
getAddress: 'getAddressList',
createAddress: 'createNewAddress',
updateAddress: 'updateAddress',
deleteAddress: 'deleteAddress'
}),

clickEdit (ind) {
console.log(this.addressList[ind], '编辑的是哪一行')
this.$router.push({
path: '/editAddress',
query: {
infoList: ind
}
})
},

addAddress () {
this.$router.push({path: '/addAddress'})
},

getAddresslist (isOne) {



address({
method: 'get',
url: '/v1/members/shipping/address?member_id='+ this.member_id +'&page='+this.pageNum+'&perpage=6',

port: 'liuyi'
}).then(res => {
let msg = res.data.data.addresses;
this.loadingShow = false;
if (res.data.result === 0) {

this.showNodata = false;
if(res.data.data.addresses.length ==0) {
this.$refs.scrollerBottom.disablePullup();
if(this.addressList.length == 0) {
this.showNodata = true;
}
flag = true;
}else if(res.data.data.addresses.length < 6){
flag = true;
this.$refs.scrollerBottom.disablePullup();
var  arr = this.addressList;
this.addressList = arr.concat(res.data.data.addresses);
}else  {
var  arr = this.addressList;
this.addressList = arr.concat(res.data.data.addresses);
this.pageNum++;
flag = false;
}
} else {
this.loadingShow = false;
}
})
},
onScrollBottom(e){
if(flag) {
return false;
}else {
flag = true;
this.loadingShow =true;
this.getAddresses();
}
console.log(e);
},

goBack(ind){
this.goodsList=this.addressList[ind];
localStorage.setItem('goodsList',JSON.stringify(this.goodsList));
if (!this.addressList) {
console.log('返回1', ind, this.addressList)
this.$router.push({path: '/addAddress'})
} else {
console.log('返回2', ind, this.addressList, this.amount_type)

if(this.amount_type == 4){
console.log('返回3', ind, this.addressList)
console.log('返回到抵扣券兑换');
this.$router.push({path: '/voucherExchange'})
} else if(this.amount_type == 2){
console.log('返回4', ind, this.addressList)
console.log ('返回到商品券兑换');
this.$router.push({path: '/goodsExchange'})
}
}
},
async deleteMyAddress (id) {
const { deleteAddress } = this
await this.$vux.confirm.show({
title: '提示',
content: '您确定要删除此收货地址吗？',
onConfirm () {
let res = deleteAddress(id)
}
})
},
async getAddresses () {
let res = await this.getAddress()
this.loadingShow = false
}
},

mounted () {
this.getAddresses()
this.amount_type  = this.$route.query.amount_type;







}
}
